.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal {
    padding: 16px;
    max-width: 980px;
    width: 100%;
    background-color: #fff;
    border-radius: 4px;
  }
