/* Стили компонента SearchForm */
.search__form {
    width: 100%;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
}

.search__form input {
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.16), 0 0 0 1px rgba(0, 0, 0, 0.08);
    border: #f0fff0;
    display: block;
    width: 100%;
    padding: 16px;
    font: inherit;
    font-size: 16px;
    border-radius: 2px;
    outline: none;
}